/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-src {
	position: relative;

	.section-bg {
		&.bg__fixed {
			background-position: 0 -100%;
		}
	}

	.editor-block-list__block & .editor-block-list__layout {
		margin-left: 0;
		margin-right: 0;
	}
}

.edit-post-visual-editor .editor-block-list__block[data-type="mkl/section-block"] {

	/* Clearfix */
	section.wp-block-mkl-section-block::before,
	section.wp-block-mkl-section-block::after {
		content: " ";
		display: table;
	}
}

.edit-post-visual-editor .editor-block-list__block[data-type="mkl/section-block"].is-selected {
	.core-blocks-spacer__resize-handler-bottom {
		bottom: calc(50% - 8px) !important;
		transform: translateX(1px);
		z-index: 400;
	}

	.spacing-box {
		padding-bottom: 1px;

		&::before {
			content: " ";
			display: block;
			height: 50%;
			width: 1px;
			border-left: 1px dotted rgba(255, 255, 255, 0.5);
			position: absolute;
			top: 0;
			left: 50%;
			visibility: hidden;
		}

		&::after {
			content: " ";
			display: block;
			height: 8px;
			width: 8px;
			border-left: 1px solid rgba(255, 255, 255, 0.5);
			border-top: 1px solid rgba(255, 255, 255, 0.5);
			position: absolute;
			top: 0;
			left: 50%;
			transform: translateX(-3.5px) translateY(1px) rotate(45deg);
			visibility: hidden;
		}

		&>span {
			&::before {
				content: " ";
				display: block;
				height: 50%;
				width: 1px;
				border-left: 1px dotted rgba(255, 255, 255, 0.5);
				position: absolute;
				bottom: 0;
				left: 50%;
				visibility: hidden;
			}

			&::after {
				content: " ";
				display: block;
				height: 8px;
				width: 8px;
				border-left: 1px solid rgba(255, 255, 255, 0.5);
				border-bottom: 1px solid rgba(255, 255, 255, 0.5);
				position: absolute;
				bottom: 0;
				left: calc(50% - 1px);
				transform: translateX(-2px) translateY(-1px) rotate(-45deg);
				visibility: hidden;
			}
		}

		&:hover,
		&:focus-visible {
			.core-blocks-spacer__resize-handler-bottom {
				display: block;
			}

			&::before,
			&::after,
			&>span::before,
			&>span::after {
				visibility: visible;
			}

			.components-resizable-box__handle::before {
				position: absolute;
				left: calc(50% - 8px);
				top: -6px;
			}
		}
	}

	.resizing {
		background: rgba(2, 133, 186, 0.2);
	}
}

.editor-bg-image {
	margin-bottom: 1em;

	.components-button+.components-button {
		margin-top: 1em;
		margin-right: 8px;
	}
}

.popup-output {
	border: 2px dashed black;
	line-height: 0.65;
	margin-bottom: 10px;
	padding: 5px;
	cursor: pointer;

	.popup-btn {
		height: 85px;
		width: 100%;
		display: block;
		background-size: cover !important;
		border-radius: unset;
		box-shadow: none !important;
		background-position: center;
		padding: 0;
	}
}

.design-option {
	.design-option-item {
		cursor: pointer;
		line-height: 0.65;
		padding-bottom: 10px;

		.design-option-btn {
			background-size: cover;
			background-position: center;
			height: 80px;
			width: 100%;
			padding: 0;
			line-height: 0.65;
			line-height: 1.3;
			border: transparent;

			div {
				pointer-events: none;
			}

			img {
				line-height: 0.65;
			}
		}

		&.selected {
			border: 2px dashed black;
			line-height: 0.65;
			margin-bottom: 10px;
			padding: 5px;
		}
	}
}

.dc-popup {
	.design-option {
		width: 100%;
		margin: auto;
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: space-between;

		.design-option-item {
			cursor: pointer;
			display: block;
			width: 32%;
			height: 100%;

			.design-option-btn-popup {
				background-size: cover;
				background-position: center;
				padding: 0;
				line-height: 0.65;
				border: transparent;
				height: 130px;
				width: 100%;

				img {
					width: 100%;
				}
			}
		}
	}
}

.dc-design-component,
.dc-media-component,
.dc-popup-component {
	.components-panel__row {
		display: block;

		button.is-primary {
			display: block;
			background: #f0f0f0;
			color: #1e1e1e;
			width: 100%;
			height: 85px;
			text-align: center;

			&:hover,
			&:focus-visible {
				background: #ddd;
				color: #1e1e1e;
			}
		}

		button.components-button {
			&.image-btn {
				height: auto;
				margin: 0;
				padding: 0;
			}
		}
	}

	.components-base-control__help {
		margin-top: calc(8px);
		margin-bottom: 0px;
		font-size: 12px;
		font-style: normal;
		color: rgb(117, 117, 117);
	}
}

.dc-media-buttons {
	margin-bottom: 15px;

	.is-secondary {
		margin-bottom: 10px;
	}
}

.dc-popup-buttons {
	margin-bottom: 15px;

	.is-secondary {
		display: block;
		margin-top: 20px;
		margin-bottom: 10px;
	}
}